"use client";

import React from "react";

const DeliveryAddressCard = () => {
    return (
        <section className="bg-white rounded-xl p-6 shadow-sm flex flex-col items-center justify-center text-center">
            <p className="font-medium mb-4">
                You don’t have any delivery address
            </p>

            <button className="bg-orange-500 text-white px-6 py-2 rounded-full text-sm">
                Add Address
            </button>
        </section>
    );
};

export default DeliveryAddressCard;
