import React from 'react';

const RecentPurchaseTracking = () => {
    const steps = [
        { label: "Order placed", completed: true },
        { label: "Order Confirmed", completed: true },
        { label: "Waiting to be Shipped", completed: true },
        { label: "Order shipped", completed: true },
        { label: "Available for pickup", completed: false },
        { label: "Delivered", completed: false },
    ];

    const currentStep = 3; // 0-indexed, so step 4 is "Order shipped"

    return (
        <div className="bg-white rounded-[20px] p-8 border border-gray-200">
            {/* Header */}
            <div className="flex justify-between items-start mb-6">
                <div>
                    <h2 className="text-xl font-semibold text-gray-900 mb-2">
                        Recent purchase tracking
                    </h2>
                    <div className="flex items-center gap-3">
                        <div className="w-16 h-16 bg-gray-200 rounded-lg"></div>
                        <div>
                            <p className="text-sm text-gray-700 font-medium">
                                The name of the product
                            </p>
                            <p className="text-sm text-gray-500">
                                Order ID: #024588
                            </p>
                        </div>
                    </div>
                </div>
                <button className="text-gray-400 hover:text-gray-600 text-2xl font-light">
                    ×
                </button>
            </div>

            {/* Estimated Arrival */}
            <div className="mb-8">
                <div className="flex items-center gap-2 mb-1">
                    <svg className="w-4 h-4 text-gray-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 8h14M5 8a2 2 0 110-4h14a2 2 0 110 4M5 8v10a2 2 0 002 2h10a2 2 0 002-2V8m-9 4h4" />
                    </svg>
                    <p className="text-sm text-gray-600">Estimated arrival</p>
                </div>
                <p className="text-lg font-semibold text-gray-900">
                    Friday 9th January 2026
                </p>
            </div>

            {/* Progress Tracker */}
            <div className="relative">
                {/* Progress Line */}
                <div className="absolute top-5 left-0 right-0 h-1 bg-gray-200" style={{ zIndex: 0 }}>
                    <div
                        className="h-full bg-green-600 transition-all duration-500"
                        style={{ width: `${(currentStep / (steps.length - 1)) * 100}%` }}
                    ></div>
                </div>

                {/* Steps */}
                <div className="relative flex justify-between" style={{ zIndex: 1 }}>
                    {steps.map((step, index) => (
                        <div
                            key={index}
                            className="flex flex-col items-center"
                            style={{ width: `${100 / steps.length}%` }}
                        >
                            {/* Circle */}
                            <div className="relative mb-3">
                                <div
                                    className={`w-10 h-10 rounded-full flex items-center justify-center transition-all duration-300 ${step.completed
                                        ? 'bg-[#077D58]'
                                        : 'bg-gray-300'
                                        }`}
                                >
                                    {step.completed && (
                                        <svg className="w-5 h-5 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={3} d="M5 13l4 4L19 7" />
                                        </svg>
                                    )}
                                </div>
                            </div>

                            {/* Label */}
                            <span
                                className={`text-xs text-center max-w-[90px] ${step.completed ? 'text-[#077D58] font-medium' : 'text-gray-500'}`}
                            >
                                {step.label}
                            </span>
                        </div>
                    ))}
                </div>
            </div>

            {/* Pagination Dots */}
            <div className="flex justify-center gap-2 mt-8">
                <div className="w-2 h-2 rounded-full bg-orange-500"></div>
                <div className="w-2 h-2 rounded-full bg-gray-800"></div>
                <div className="w-2 h-2 rounded-full bg-gray-800"></div>
            </div>
        </div>
    );
};

export default RecentPurchaseTracking;